/* Header */
/* 
    Restyles link bar (Alerts, Favorites, User Greeting, QuickNav) and workspace title
*/

/* Workspace Title */
.CaseTitleHeaderTextClass {
	color:$workspaceTitleColor;
	font-weight:400;
	font-size: 16px;
	line-height: 31px;
}

/* Link bar */
.userGreetingAnchor {
	span[title="Favorites Dropdown Menu"] > span,
	span[title="User Dropdown Menu"] > span {		// Style link bar items
		color:$linkBarItemColor;
		font-weight:600;
		&:hover {
			color:$linkBarItemColorHover;
		}
	}
}

.RelativityHeaderAlertWithHand {	// Alert link bar item
	color:$linkBarAlertItemColor;
	font-weight:600;
	&:hover {
		color:$linkBarAlertItemColorHover;
	}
}

.filters-row input[type="button"]{
	height: 24px;
	border-style: solid;
	border-width: 1px;
	border-radius: 3px;
	color: $blueDark;
	background-color: $defaultWhite !important;
	border-color: $defaultBorderColor;
}

.multilist-filter-container .blueFilterButton{
	@extend .buttermilk-button-positive;
	height:24px !important;
	padding: 0 !important;
	outline: 0 !important;
	border-color:$buttonBorderColor !important;
	box-sizing:border-box !important;
}

.multilist-filter-container .blueFilterButton:hover{
	height:24px !important;
	padding: 0 !important;
	outline: 0 !important;
}

.multiListFilterHeader {
	border: none !important;
}

.multiListFilter > tbody > tr > td {
	padding: 0 !important;
}

.multilistFilterChoices {
	padding: 2px 8px;
}

.multiListFilter, .itemTable table.multiListFilter {
    position: fixed;
}

.locked-header {
	overflow: visible;
}

.multiListFilter .multiListFilterHeaderContainer .multiListFilterHeader {
	padding: 4px 5px !important;
}

a.userGreetingAnchor span,
.RelativityHeaderAlertWithHand {
	text-decoration:none;
	&:hover {
		text-decoration:underline;
	}
}

.quickNavIcon {
	background-position: 0 -1px;
}

.associative-list .multilist-filter-container .multiListFilter{
	left: auto !important;
	position: absolute !important;
	border: solid 1px $defaultBorderColor !important;
	> tbody > tr > td {
		background-color: $defaultBGColor !important;
	}
	.multilistFilterChoices, .multiListFilterHeader{
		color: black;
		font-weight: 500;
	}
}

.headerRelativityLogoButton { // relativity logo in header for new nav widget
	width: 41px;
}

.navWidget { // add top margin to nav widget (not anymore)
	//margin-top:4px; 
}

.CaseTtitleHeaderTextClass { // slightly reduce font size of case title to accommodate new nav widget
	font-size:17pt;
}

.textEllipsis{
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.headerOuterTable td.headerOuterTableMainCell{
	padding: 0;
}

.headerOuterTableMainCell .headerContainer{
	padding: 6px 0 5px 0;
	margin-right:20px;
	&.brandingImageToRight {
		margin-right:15px;
	}
}

.headerOuterTableMainCell .leftAligned{
	padding-top: 1px;
	height: 28px;
}

.headerUpperRow{
	height: 29px;
	.brandingImageContainer {
		float:right;
		margin-right:20px;
		height:100%;
		img {
			margin-top:2px;
		}
	}
}

#user_flyout_new_ui {
    font-weight:600;
    margin-bottom:-1px;
}

.switchUIIcon {
    display: inline-block;
    background-repeat: no-repeat;
    background-position:0 1px;
    width: 16px;
    height: 17px;
    vertical-align: top;
    background-image: url("../Images/switchToFluid.png");
    margin-right:7px;
    margin-top:-1px;
}