/*Mass Save as PDF*/

#popupScrollingDiv
  	{
			overflow: auto;
  	}  	

  	.sortableListOuterDiv {
  		border-radius: 3px;
  		border: 1px solid $defaultBorderColor;
  		background: white;
  		overflow: hidden;
  		position: relative;
  		padding: 10px 10px 7px 10px;
  		max-height: 250px;
  		overflow-y: auto;
  		overflow-x: hidden;
		&.notScrollableListOuterDiv{
			overflow: hidden;
		}
		.sortableList {
  			list-style-type: none;
  			margin: 0;
  			padding: 0;
  			width: 100%;
			.sortableListItem {
  				background: $defaultBGColor;
  				color: $gridFontColor;
  				border: 1px solid $defaultBorderColor;
  				color: black;
  				font-size: 12px;
  				padding-top: 3px !important;
  				padding-bottom: 3px !important;
  				padding: 3px 5px 3px 17px;
  				font-weight: normal;
  				cursor: pointer;
  				margin: 0 0 3px 0;
  				outline: 0;
				word-wrap: break-word;
				word-break: normal;
				&:hover{
					background-color:$gridHoverBGColor !important;
					.dragHandle {
  						background-image: url(../Images/Draggie_Blue.png) !important;
  					}
				}
				&:focus{
					background-color:$gridHoverBGColor;
					.dragHandle {
  						background-image: url(../Images/Draggie_Orange.png) !important;
  					}
				}
				.dragHandle {
					position: absolute;
  					margin-left: -14px;
					height: 18px;
  					background-image: url(../Images/Draggie.png) !important;
  				}
  			}
  		}
  	}

  	.modify-production-order
  	{
			margin-top: 4px;
  	}

	.printPDFSelect {
		width: 280px;
	}

