li.viewSectionTitle.ui-state-default.ui-corner-top {
    text-decoration: none!important;
    background: #e2e2e2!important;
    font-family: 'arial';
    top: 0px!important;
    margin: 0!important;
    border-left: 0;
    margin-bottom: 0px!important;
    border-right: thin solid #e2e2e2;
    /*border-bottom: thin solid #fcfcfc!important;*/
    
    border-top: thin solid #e2e2e2!important;
    padding-left: 1px!important;
    padding-right: 1px!important;
    padding-bottom: 1px!important;
    &.ui-tabs-selected.ui-state-active {
        top: 0px;
        background: #fcfcfc!important;
    }
    &.ui-tabs-selected.ui-state-active:hover {
        background: #fcfcfc!important;
    }
    &.ui-tabs-selected.ui-state-active a {
        Font-weight: bold!important;
        color: #3471b7!important;
        &:hover {
            text-decoration: none !important;
        }
    }
    &:hover {
        border-right: thin solid #e2e2e2;
        border-top: thin solid #e2e2e2;
        background: #fcfcfc !important;
    }
    a {
        font-weight: normal!important;
        color: #1b5495!important;
        &:hover {
            text-decoration: underline!important;
        }
    }
}

