

/*browser*/

.paneTable{
	background:$defaultWhite;
	border-bottom-color:$defaultBorderColor !important;
}
.treeViewc, .iconRowTable, .FoldersTable{
	background:$defaultBGColor;
}
.documentFolderPanelHeader {
	background: $grayLight;
}

.showHideSearchBrowserCell {
	display: none;
}

.folderCell #dragHandle, td#dragHandle {
	background: url(/relativity/Images/drag-handle.png) no-repeat center center;
	min-width: 10px;
	max-width: 10px;
	width: 10px;
	border-left: 1px solid $defaultBorderColor;
	border-right: 1px solid $defaultBorderColor;
}

.folderCell #expandHandle, td#expandHandle {
	background: url(/relativity/Images/Browser_Open.png) no-repeat center top 5px;
	min-width: 18px;
	max-width: 18px;
	width: 18px;
	cursor: pointer;
}

.RadTreeView .rtIn {
	margin-left: 3px !important;
}

.RadTreeView_Default .rtIn{
	color: $blueDark !important; //$gridHeaderFontColor !important;
	font-weight: 500;
}

.RadTreeView_Default .rtIn:hover{
	background-image: none !important;
	background: none !important;
	border: 1px solid $blueLight !important;
	//box-shadow: 0 0 0 4px $blueShadow !important;
}



.RadTreeView_Default .rtSelected .rtIn{ 	
	margin-left: 3px;
	background-image: none !important;
	border-color: $flyoutMenuItemBGColorHover !important;
	background-color: $flyoutMenuItemBGColorHover !important;
	font-weight:600;
}

.RadTreeView_Default .rtIn input{ 	
	position: relative;
	background-image: none !important;
	background-color: $flyoutMenuItemBGColorHover !important;
}

.paneTable{
	height: 31px;
}

.paneTable td{
	padding-top: 4px;
}

.paneTable td.collapseExpandCell {
	padding-left: 5px;
}

.paneTable td.paneText {
	padding-top: 3px;
	text-transform: uppercase;
	color: $blueDark !important; //$gridHeaderFontColor !important;
}

.rtPlus{
	margin-top: 5px !important;
	margin-left: -17px !important;
	background-color:$defaultBGColor; 
	background-image: url('/Relativity/Images/Browser_Plus.png') !important;
	background-position: 0px 0px !important;
}
.rtMinus{
	margin-top: 5px !important;
	margin-left: -17px !important;
	background-color:$defaultBGColor; 
	background-image: url('/Relativity/Images/Browser_Minus.png') !important;
	background-position: 0px 0px !important;
}

.rtPlus:hover{
	background-image: url('/Relativity/Images/Browser_Plus_Hover.png') !important;
	background-position: 0px 0px !important;
}
.rtMinus:hover{
	background-image: url('/Relativity/Images/Browser_Minus_Hover.png') !important;
	background-position: 0px 0px !important;
}

.RadTreeView_Default input[type="checkbox"]+label+img{
	margin-left: 5px;
}

.iconRowTable {
	border-top-color:$defaultBorderColor;
}

.hiddenForAnimation {
  display: none;
}

.visuallyHiddenForAnimation {
  opacity: 0;
}

#_moveableFolderCell {  
  transition: all 0.4s linear;  
}

#expandHandle {
	border-right:1px solid $defaultBorderColor;
}

#_folders_TreeView:focus,
#_codes_TreeView:focus,
#_searchContainers_TreeView:focus,
#_clusters_TreeView:focus {
	outline:none;
}

.RadTreeView .rtUL{
	margin: 0 !important;
}

.saved-search-listview .search-pane-div tr[id$="_itemList_bottomActionRow"] .actionCellContainer.rightAligned {
	.itemListActionCell:first-child {
		padding-top:9px;
		padding-bottom:0px;
	}
	.itemListActionCell:nth-child(2) {
		padding-top:3px;
		padding-bottom:3px;
		line-height:20px;
		span.itemListTopPaginationControls:first-child {
			padding-top:4px;
			padding-bottom:4px;
		}
	}
	input[type="image"] {
		margin-top:9px;
	}
}


.saved-search-listview .massEditBarContainer select{
	margin-left: 2px;
}

.saved-search-listview .massEditBarContainer a{
	margin-top: 2px;
}

.panes-collection #_paneCollection_iconCell {
	min-height:28px;
}

.browser-button-collection {
	background: $defaultBGColor;
	text-align: center;
  height: 22px;
  padding-top: 3px;
  padding-bottom: 3px;
  line-height: 22px;
}

.cluster-browser {
	height: calc(100% - 42px);
}

.cviz-button-collection {
	@extend .browser-button-collection;
	height: 36px;
	text-align: left;
}


/* end browser*/