/* Viewer */
.reviewContainerTable .links {
	background:$activeTabBGColor;
	border-bottom-color:$defaultBorderColor
}

.returnText {
	color: $activeTabColor;
	font-weight:600;
	text-decoration:none;
	&:hover {
		text-decoration:underline;
	}
}

.documentIdentifierRow,
.documentNavigatorTable,
.reviewActionBarTop,
.reviewActionBarBottom,
.reviewIconRowTable,
.reviewPaneTable
 {
	background: $defaultBGColor;
}

.documentIdentifierRow {
	border-bottom-color:$defaultBorderColor
}

.documentNavigatorTable tr td {
	padding: 1px 1px 1px 1px;

}

.reviewActionBarGroup {
	margin: 1px 0 2px 0;
}

.viewer-selector {
	padding: 2px 0 1px 9px;
	height:25px;
	background-color:$defaultBGColor;
}

td.documentNavigatorNonButtonCell input[type="text"]{
	padding:0;
	width: 30px;
}

.viewTemplateErrorMessageRelational{
	background-color:$defaultWhite;
	color: $flyoutAlertLabelColor;
}

.document-review-drag-handle{
	margin-left:0 !important;
	margin-right:0 !important;
}

.reviewActionBarTop,
.reviewActionBarBottom{
	min-height:26px;
	/*padding-bottom:1px; This fixes alignment of top bars in the viewer...*/
	border-color:$defaultBorderColor;
}

.dynamicActionBarTop {
	min-height:28px;
	.dynamicActionBarButtonGroup {
		margin:2px 4px 2px 0;
		#_documentProfileEditor_copyBtn_button[disabled] {
			padding:4px 8px 4px 8px;
		}
	}
}

.document-review-drag-handle{
	background: url(/relativity/Images/drag-handle.png) no-repeat left center !important;
	min-width: 10px !important;
	max-width: 10px !important;
	width: 10px !important;
	border-left: 1px solid $dragHandleDividerColor;
	border-right: 1px solid $dragHandleDividerColor;
}

.documentNavigatorTable,
.reviewIconRowTable {
	border-color:$defaultBorderColor;
	padding-top: 3px !important;
	padding-bottom: 1px !important;
}

#_moveablePaneCollectionRow td {
	border-top:$defaultBorderColor 1px solid;
}

span#_documentViewer .document-identifier {
	border-bottom-color:$defaultBorderColor;
}

#_documentIdentifier_ReviewLabel {
	margin-left:6px;
}

#_documentIdentifier_FileIconLink {
	margin-left:8px;
}

.documentIdentifierRow {
	padding-bottom: 0;
	a img{
		margin-top: 1px;
	}
}

.massEditBarContainer {
	background-color:$defaultBGColor;
	border-top-color:$defaultBorderColor;
}

.reviewPaneTable {
	border-bottom:1px solid $defaultBorderColor !important;
}

.relational-list .itemListTable {
	border-top-color:transparent;
}

.reviewPaneText {
	font-weight:600;
}

#Documentreviewcontextnavigator1__contextTable {
	margin-left: 7px;
	img{
		margin-top: 2px;
	}
}

.reviewContainerTable .links {
	height: 24px;
	padding: 6px 0 0;
}

.viewer-selector select,
.viewer-selector .button-container{
	margin-top: -2px;
}

.viewer-selector .button-container select {
	margin-top: 0;
}

.reviewPaneTable .collapseExpandCell{
	display: none;
}

.relational-list {
	tr[id$="_itemList_bottomActionRow"] .actionCellContainer.rightAligned {
		.itemListActionCell:first-child {
			padding-top:9px;
			padding-bottom:0px;
		}
		.itemListActionCell:nth-child(2) {
			padding-top:3px;
			padding-bottom:3px;
			line-height:20px;
			span.itemListTopPaginationControls:first-child {
				padding-top:4px;
				padding-bottom:4px;
			}
		}
		input[type="image"] {
			margin-top:3px;
		}
	}
}

.viewer-selector .ArtifactTypeList input {
	top: 0;
	vertical-align: middle;
}

.viewer-selector .ArtifactTypeList label {
	vertical-align: middle;
	position: relative;
	top: -1px;
}

.panes-collection .reviewIconRowTable#_paneCollection_iconCell{
	border-bottom-color: $defaultBGColor;
}

/* increase data density on doc coding pane */
#_main #_documentProfileEditor__kCuraScrollingDiv .documentProfileTemplateContainer {
	margin-left:4px;
	margin-right:4px;
}
#_main #_documentProfileEditor__kCuraScrollingDiv .childViewTable>tbody>tr>td>div.associative-list,
#_main .childViewTable>tbody>tr>td>div.associative-list {
	margin-left:1px;
	margin-right:1px;
}

.documentProfileEditMode .editTable .documentEditTableColumn > table{
	border-spacing: 0 2px !important;
	td.viewFieldNameProfile,
	td.editFieldNameProfile{
		padding-right: 3px;
	}
	td.editFieldNameRequiredProfile,
	td.editFieldNameRequired{
		//background: $cardRequiredBGColor;
		border-color: $cardBGColor !important;
		border-width: 3px !important;
		padding-right: 4px;
		background-repeat: repeat-y;
		background-image: url("../Images/Required_BG.png");
		background-position: right 10%;
	}
		//Comment in if we put field labels blue back on hover START
	//> tbody > tr:hover > td{
		//background-color: $cardProfileHoverColor;
		//border-color: $cardProfileHoverColor;		
		//.PopupPickerDescriptionSpan{
		//	background-color: $cardProfileHoverColor !important;
		//}
		//&.editFieldNameRequiredProfile,
		//&.editFieldNameRequired{
		//	//background-color: $cardRequiredHoverColor;
		//	border-color: $cardProfileHoverColor !important;
		//}		
		//Comment in if we put field labels blue back on hover END
		//Comment out if we put field labels blue back on hover START
	> tbody > tr > td:hover{
		&.editFieldValueProfile,
		&.viewFieldValueProfile{
			background-color: $cardProfileHoverColor;
			border-color: $cardProfileHoverColor;
		}				
		.PopupPickerDescriptionSpan{
			background-color: $cardProfileHoverColor !important;
		}	
		//Comment out if we put field labels blue back on hover END
	}
	td.editFieldValueProfile,
	td.viewFieldValueProfile {
		padding-left:1px;
	}	
		
}

#_main #_documentProfileEditorPanel {
	.editTable { // card headers
		box-shadow: 1px 1px 2px $defaultBoxShadow;
		padding-top:0;
		margin-top:4px;
		padding-left:0;
		padding-bottom: 7px;
		.viewSectionTitle {
			padding-left:3px;
			margin-top:0;
			padding-top:0px;
			padding-bottom: 0;
			margin-bottom: 0;
			font-size: 12px;
			.helpIcon {
				margin-top: 1px;
			}
		}
		&.collapsed {
			padding-bottom:2px;
		}
	}
	&.reducedFieldLabelMaxWidth { // this class is applied after label wrapping is conditionally applied
		td.viewFieldNameProfile,  // it is the target max-width for field labels, but we have to initialize
		td.editFieldNameProfile,  // the value to 201px first
		td.editFieldNameRequiredProfile,
		td.editFieldNameRequired { // field labels
			max-width:200px;
		}
	}
	td.viewFieldNameProfile,
	td.editFieldNameProfile,
	td.editFieldNameRequiredProfile,
	td.editFieldNameRequired { // field labels
		min-width:0px;
		max-width:201px;
		font-size:11px;
		font-weight:bold;
		&.wrapFieldLabel {
			min-width:150px;
			white-space:normal;
			word-break: normal;
			word-wrap: break-word;
		}
		img {
			max-width:98%;
		}
	}	
	td.viewFieldValueProfile,
	td.editFieldValueProfile { // field values
		font-size:11px;
	}
	td.editFieldValueProfile {
		border-bottom-style: solid !important;
		border-bottom-width: 1px !important;
		border-bottom-color: $cardBorderColor !important;
		td,
		input[type="text"],
		textarea {
			font-size:11px;
		}
		.RadioButtonListRelativePosition {
			border-spacing:0px !important;
			width:auto !important;
		}
	}
	.documentEditTableColumn { // field labels
		padding-left:6px;
	}
	.itemListActionCell { // associative list card headers
		margin-left:5px;
		.childViewSectionTitleText {
			margin-left:-4px;
		}
	}
	.childViewTable > tbody > tr > td > div.associative-list {
		box-shadow: 1px 1px 2px $defaultBoxShadow;
	}
	.childViewTable {
		border-left: solid 2px transparent;
		border-right: solid 2px transparent;
		> tbody > tr:first-child > td:empty {
			display:none;
		}
	}
}

#_documentProfileEditorPanel .reviewTemplateErrorMessage{
	margin-top: 2px;
	margin-bottom: 2px;
}

#_fileTypeSelector_productionsDropDown{
	max-width: 500px;
}

.panes-collection img.pane-type{
	max-height: 16px;
}
/* End Viewer */