/*begin text*/
body{
	color:$defaultFont;
	background:$defaultWhite;
}
html, body, div, p, a, label, select, input, * {
	font-family: $defaultFontFamily;
}

a {
	font-weight: 400;
	color: $blueLink;
	//text-decoration: underline;
	text-decoration: none !important;
}

a.consoleLinkEnabled {
	//text-decoration: underline;
	text-decoration: none;
}

a:hover {
	color: $blueDark;
	text-decoration: underline !important;
}

a.consoleLinkDisabled {
	color:$consoleLinkDisabledColor;
	&:hover {
		color:$consoleLinkDisabledColor;
		text-decoration:none !important;
	}
}

.itemListPrimaryLink {
	font-weight: 600;
}

.chvronTextOnTheRightSpan {
	color:$blueBright;
}
td,th{
	font-size:$defaultFontSize;
}

.pageBaseHeaderCssClass {
	background:$defaultWhite;
}

.logout #_cell {
	strong {
		font-weight:500 !important
	}
	a {
		font-weight:700 !important;
	}
}

#workspaceVersionText {
	margin-top:40px;
	font-size:15px;
	font-weight:400;
}

#workspaceVersionReturnHome {
	font-size:15px;
	font-weight:700 !important;
}
/*end text*/