/* Quick Search start */

.pivotmessagebar[id$="_quicksearchmessagebar"] {
	background-color:$samplingMessageBarBGColor;
	color:$samplingMessageBarColor;
	margin-left:1px;
}

.quickSearchContainer,
.quickSearchContainer > table {
	background-color:$grayLight;
	padding-bottom:5px;
}

.quickSearchContainer {
	border-top:1px solid $defaultBorderColor;
	padding-left:3px;
	input[type="button"] {
		height:22px;
	}
}

.quickSearchCriteriaFieldsHeader {
	display:none;
}

.quickSearchContainer .criteriaConditionsValueCell {
	width:100%;
}

.quickSearchCriteriaTable {
	border:1px solid $defaultBorderColor !important; // needs !important
	background-clip:padding-box;
	background-color:$defaultWhite;
	tr {
		border-right-color:$defaultBorderColor;
		border-left-color:$defaultBorderColor;
		border-top-color:$defaultBorderColor;
		border-top-width:1px;
	}
	td {
		border-color:$defaultBorderColor;
	}
	textarea, select:enabled, input[type="text"], div[id$="_Description"] {
		color:$blueDark;
	}
	.quickSearchCriteriaFields:nth-child(3) select {
		width:145px;
	}
	input[type="text"] {
		padding:1px;
		height:19px;
	}
}

.quickSearchCriteriaFields {
	select[id$="_OpenParensField"] {
		margin-left:8px;
		margin-right:8px;
		min-width:50px;
	}
	select[id$="_CriteriaField"] {
		margin-right:8px;
	}
	select[id$="Operator"] {
		margin-right:8px;
	}
	select[id$="_CloseParensField"] {
		margin-left:8px;
		margin-right:8px;
		min-width:50px;
	}
	select[id$="_AndOrListField"] {
		margin-right:8px;
		min-width:60px;
	}
	a[id*="_ClearLink"] {
		margin-right:8px;
	}
	div[id$="_Description"] {
		border:1px solid $defaultBorderColor;
		border-radius:3px;
		padding:1px;
		height:18px;
	}
}

.quickSearchCriteriaFields.criteriaConditionsValueCell div {
		margin-right:8px;
}

.quickSearchContainer .SearchTable >tbody > tr > td:nth-child(3){ 
	padding-top: 8px !important;
	padding-right: 22px !important;
}

.SearchTableNoBorder .SearchTable >tbody > tr > td:nth-child(2), 
.SearchTable .SearchTable >tbody > tr > td:nth-child(2){ 
	padding-right: 16px !important;
}

.caBasicName {
	width: 92px;
	padding-top: 3px;
}

table.SearchTableNoBorder > tbody > tr > td {
	padding:0;
}

.caBasicName {
	font-weight:600;
}

td[id$="_searchCell"] > .SearchTable {
	border-top: 1px solid $defaultBorderColor;
}

/* Quick Search end */