#_informationTable .fieldTable,
#_searchTextTable .fieldTable,
#_conditionsTable .fieldTable {
	max-width: 100%;
}

#_sortTable select{
	margin: 2px;
}

#_searchTextText,
#_conditionsText,
#_fieldsText,
#_sortText
{
	display: inline;
}

#_searchTextText + img,
#_conditionsText + img,
#_fieldsText + img,
#_sortText + img
{
	margin-top: 2px;
}

#_viewCriteriaCollectionField .criteriaFields {
	background-color: $defaultBGColor;
}

#_viewCriteriaCollectionField_viewCriteriaCollection_CriteriaTable {
	table-layout:auto;
	.criteriaFields:nth-child(3) select{
		width:145px;
	}
	.criteriaConditionsValueCell {
		width:100%;
	}
}

.manualReloadMessage {
	background-color: $defaultBGColor;
	border-bottom: 1px solid $defaultBorderColor;
}

.cardSectionHeader#_searchTextDiv,
.cardSectionHeader#_conditionsDiv{
	padding-left: 0;
}

#_othersDiv	.editTable{
	margin: 0;
}

#_othersDiv	.editTable .editFieldValue{
	padding-left: 9px !important;
}

.searchErrorMessage {
	background-color: $defaultBGColor !important;
}

#_conditionsTable .editFieldValue{
	padding-left: 8px;
	padding-right: 8px;
}

#_sortTable {
	margin-left: 11px;
}

.criteriaFieldsHeader{
	font-weight: 600;
}

#_searchTextTable #_searchProviderDropdown #_searchProviderDropdown_nameCell{
	border-right: 0px;
}

.cardContainerSavedSearch {
	padding-right: 12px !important;
	#_informationTable .fieldTable input[type=text]{
		max-width: 460px;
	}
}