.itemTable,
div.tableDiv > .itemListTable {
	border-spacing: 0;
	border-collapse: collapse;
	table-layout:fixed;
	td, td.itemListHead {
		border-bottom-style: solid;
		border-bottom-width: 1px;
		border-top-style: solid;
		border-top-width: 1px;		
		border-spacing:0;
		border-collapse:collapse;
		border-right: 1px dotted;
		border-color: $gridBorderColor;
		border-left-color: transparent;
		border-right-color: transparent;		
		&:first-child{
			border-left-color: $gridBorderColor;
		}
		&:last-child{
			border-right-color: $gridBorderColor;
			border-right-style: solid;
		}		
	}
	&.itemTableColumnBorders td{
		border-right-color: $gridBorderColor;
		border-right-color: $gridBorderColor;
		border-right-style: solid;
	}
	td.itemListHead {
		border-color: 1px solid $gridHeaderBorderColor;
	}
	tbody tr:first-child td{
		border-top-color: $gridBGColor;
	}
	th{
		padding-left: 6px !important;
		padding-right: 6px !important;	
	}
	tbody td {
		padding-left: 6px !important;
		padding-right: 6px !important;
		padding-top: 7px !important;
		padding-bottom: 4px !important;
	}

	th > a > img, td > a > img {
		display: block;
		margin: auto;
	}
	> tbody > tr > {
		td:first-child, th:first-child {
			border-left: solid thin $gridBorderColor;
		}
	}
	> tbody > tr:hover{
		background-color:$gridHoverBGColor !important;	
		 > td, th, .itemListRowNum {
			//border-bottom: solid thin $gridHoverBorderColor;
			//border-top: double thin $gridHoverBorderColor;
			background-color:$gridHoverBGColor !important;
		}
		// > td:first-child, th:first-child {
		//	border-left: solid thin $gridHoverBorderColor;
		//}
		// > td:last-child, th:last-child {
		//	border-right: solid thin $gridHoverBorderColor;
		//}
	}
}

