
/*login*/
.login-container,
.forgot-password-container {
	background:$defaultBGColor;
	margin: 20px 0 10px;
	border: thin solid $defaultBorderColor;
	//-webkit-box-shadow: 10px 10px 5px $defaultBorderColor;
	//-ms-box-shadow: 10px 10px 5px $defaultBorderColor;
	//box-shadow: 2px 2px 1px $defaultBoxShadow;
	-ms-border-radius: 3px;
	border-radius: 3px;
	overflow:hidden;
	.cardLabel{
		font-size:.9rem !important;
	}
	td.right {
		width: 180px;
		color: $grayDefault;
		background-color: transparent;
		
	}
	>tbody>tr:first-child>td{
		padding-top:30px;
	}
	>tbody>tr:last-child>td{
		padding-bottom:30px;
	}
	>tbody>tr>td{
		padding-left:30px;
		padding-right:30px;
	}

}

.login-body,
.forgot-password-body{
	background:$bluePrimary;
}

#_newPassword, #_retypePassword{
	.loginerrormessage.caps-lock{
		position: relative;
		border-color: $grayDefault !important;
		&:before{
			margin: 0;
			top: -10px;
			left: 2px;
		}
	}
}