

/* console */
.consoleContainer,
#_licenseInfoContainer td.rightNavActionBar{
	overflow:hidden;

	min-width: 200px;
	right:0;
	height:100%;
	width: 200px;
	background-color: $cardBGColor !important;
	border: thin solid $cardSectionBorder;
	vertical-align:top;
	//padding-top:15px;
}

.consoleContainer {
	border-top-width:0;
}

.ConsoleControlTitle
{
	text-align: left;
	font-weight: bold;
	//font-weight:600;
	font-size: 10pt;
	text-transform: uppercase;
	border-style: solid solid none solid;
	border-width: 1px;
	border-color: silver;
	background: $consoleBGColor;
	//color:$blueLight;
	color:$cardSectionHeaderFont;
	margin:0;
	padding-left:10px;
	.rightNavActionBar &,
	.consoleContainer & {
		border-style: none none solid none !important;
		box-shadow: none ;
		margin:0;	
	}
}

.ConsoleControl
{
	text-align: left;
	font-weight: 400;
	font-size: 10pt;
	border-style: solid;
	border-width: 1px;
	border-color: silver;
	background: $consoleBGColor;
	margin:0;
	padding:0;
	.rightNavActionBar &,
	.consoleContainer & {
		border: none;
		box-shadow: none ;

	}
	hr {
		background-color:$defaultBorderColor;
		color:$defaultBorderColor;
		border-width:0;
		height:1px;
	}
}

.consoleHeader
{
	text-align: left;
	font-weight: 600;
	font-size: 10pt;
	background: $consoleBGColor;
	color:$consoleHeaderFontColor;
	margin-left:10px;
}

hr.consoleSeparator{
	width:100%;
	background-color:$defaultBorderColor;
	color:$defaultBorderColor;
	border-width:0;
	height:1px;
}

.consoleLinkEnabled,
.consoleLinkDisabled {
	margin-left:0;
}

.ConsoleControlTitle {
	margin-top: 9px !important;
	border-bottom: 0;
	font-size: 14px;
}

.consoleLinkDivPopup > .consoleLinkDisabled{
	width:100%;
	background: $consoleBGColor url('../Images/pop_up_icon_disabled.png') no-repeat;
	background-position:77%;
}

