.rightIconConsoleLinkDiv .consoleLinkDisabled {
	background: url('../Images/pop_up_icon_disabled.png') no-repeat;
	background-position:80%;
}
*/
.consoleLinkDiv > span.consoleLinkDisabled > span.popUpIcon {
	background:url('../Images/pop_up_icon_disabled.png') no-repeat;
}

.binders-console-status {
	width:250px !important;
}

