/*Search criteria*/
.SearchTable {
	background: $defaultBGColor;
}

.criteriaFieldsHeader,.criteriaFields{
	background: $defaultBGColor;
}

a.childViewSectionTitleButton[id$='addCriteria_button']{
	margin-left: 14px;
}

div[id$='stepThree'].cardSection table[id$='criteriaGroup_CriteriaTable']{
	border-left: 11px solid $defaultBGColor;
