.genericMessageBarAlert,
.genericMessageBarSuccess,
.genericMessageBarError
{
	@extend .cardTable;
	margin-left:0;
	margin-right:0;
	border-style:none;
}

.genericMessageBarSuccess{
	background-color: #e1fce4;
}
.genericMessageBarError{
	background-color: #fce9e1;
}
.genericMessageBarAlert{
	background-color: #f7e2ae;
}

#_results_itemList_tableDiv{
	border-style:none solid solid solid;
	border-color: $defaultBorderColor;
	border-width: thin;
}

.cardRelativityControl.newTermsSTR {
	margin-top: 22px;
}

.cardRelativityControl.newTermsSTR .dictionary{
	margin-left: 5px;
}

.cardRelativityControl.newTermsSTR textarea{
	margin-top: 4px;
}