/* Tabstrip work */

/* Main Tab Strip */
#tabstrip-wrapper {	// move tabstrip up
	padding-top:1px;
	height:30px;
	margin-top:1px;
}

.tabstripLeftMargin {
	margin-left: 79px;
}

.tabstrip {
	height:30px;
}

.tabstrip,
.subtabstrip {
	a {
		//outline:none !important;
		text-decoration:none !important;
	}
	a:focus {
		outline-style:none;
		//background-color:$inactiveTabBGColorHover !important;
		//box-shadow:none !important;
		//border:none !important;
	}
}

.tsItemSelected { // removed css On
	vertical-align:top !important; // needs !important
	&:focus,
	&:active {
		span {
			background-color:$inactiveTabBGColorHover;
		}
	}
}

.cssTabStrip_Link_LevelOne.cssTabStrip_On {
	&:focus,
	&:active {
		span > span {
			background-color:$inactiveTabBGColorHover;
		}
	}
}

.tsCenterCellSelected { 
	color:$activeTabColor;
	background-color:$activeTabBGColor;
	height:26px !important; // needs !important
	padding-top:4px !important; // needs !important
	.cssParentDropArrow {
		margin-top:-1px;
	}
}

.tabstrip a.tsCenterCellSelected {
	&:focus,
	&:active {
		background-color:$inactiveTabBGColorHover !important;
		> span {
			background-color:$inactiveTabBGColorHover !important;
		}
	}
}

.tsItem,
.tsItemFirst,
.parentTabStyleFixed,
.tsItemPostSelected {	// increase inactive tab height
	margin-top:2px !important; // needs !important
	padding-top:0px !important; // needs !important
	padding-bottom:5px;
	height:22px !important; // needs !important
	background-color:$inactiveTabBGColor;
	&:hover,
	&:focus,
	&:active {
		color:$inactiveTabColorHover !important;
		background-color:$inactiveTabBGColorHover !important;
		.tsCenterCell, .tsCenterCellFirst {
			color:$inactiveTabColorHover !important;
			background-color:$inactiveTabBGColorHover !important;
			border-top-color:$inactiveTabBGColorHover !important;
		}
	}
}

.parentTabStyleFixed.cssTabStrip_On {
	vertical-align:top;
	margin-top:0 !important;
}

.cssTabStrip_Link_LevelOne:hover .tsCenterCell,
.cssTabStrip_Link_LevelOne:focus .tsCenterCell,
.cssTabStrip_Link_LevelOne:active .tsCenterCell,
.cssTabStrip_Link_LevelOne:hover .tsFirstTabCenter,
.cssTabStrip_Link_LevelOne:focus .tsFirstTabCenter,
.cssTabStrip_Link_LevelOne:active .tsFirstTabCenter {
	background:$inactiveTabBGColorHover !important;
	color:$inactiveTabColorHover;
}

.tabstrip .tsCenterCell,
.tabstrip .tsCenterCellSelected,
.tabstrip .tsCenterCellFirst {	// space out tabs
	padding-left: 12px;
	padding-right: 12px;
	white-space: normal;
}

.tabstrip > a,
.tabstrip > span,
.tabstrip .tsItem,
.tabstrip .tsItemPostSelected,
.tabstrip .cssTabStrip_Link_LevelOne {
	margin: 0 -0px 0 0;
}

.parentTabStyleFixed {
	vertical-algin:top !important;
}

.tsCenterCell,
.tsCenterCellFirst {
	padding-top:7px !important;
}

.tsCenterCell,
.tsCenterCellFirst,
.cssTabStrip_Link_LevelOne {
	color:$inactiveTabColor;
	background-color:$inactiveTabBGColor;
	vertical-align:top !important;
	&:hover,
	&:focus,
	&:active {
		color:$inactiveTabColorHover !important;
		background-color:$inactiveTabBGColorHover !important;
		border-top-color:$inactiveTabBGColorHover !important;
		.parentTabStyleFixed {
			color:$inactiveTabColorHover !important;
			background-color:$inactiveTabBGColorHover !important;
			border-top-color:$inactiveTabBGColorHover !important;
		}
	}
}

.cssTabStrip_On > .cssTabStrip_Link_LevelOne { // fixes after dropdown subtab selected
	font-weight:400;
}

.tsCenterCell,
.tsCenterCellFirst {		// top border of main tabs
	border-top-color:$inactiveTabBGColor;
	padding-bottom:7px !important;
}

.headerOuterTableMainCell {				/* bottom border of main tab strip */
	border-bottom:0px solid $inactiveTabBGColor;
}

#headerOuterTableParentPlaceholderRow{
	border-bottom:27px solid $inactiveTabBGColor;
}

#headerOuterTableChildPlaceholderRow{
	border-bottom:27px solid $inactiveSubTabBGColor;
 }

.headerOuterTableMainCell.headerOuterTableMainCellNoBorder {				/* bottom border of main tab strip */
	border-bottom:0;
}

/* Scroll Arrows */
#tabstrip-wrapper > .leftAlignedSpecial,
#tabstrip-wrapper > .rightAligned {
	margin-top:0px !important;
	background-color:$inactiveTabBGColor !important;
	height:20px !important;
}

#tabstrip-wrapper > .leftAlignedSpecial:hover,
#tabstrip-wrapper > .rightAligned:hover{
		background-color:$inactiveTabBGColorHover !important;
	}

#tabstrip-wrapper > .leftAlignedSpecial,
#tabstrip-wrapper > .rightAligned {			// hide selected tab when hanging off edge (required subtracting 2px from top margin)
	border-top:2px solid $defaultWhite;
}

.tabstripScrollButton {
	padding-top:3px !important;
}

.headerOuterTableMainCell .rightAligned {
	left: -29px;
	width:25px;
}

/* Dropdown Arrows */
.cssParentDropArrow
{
	background: transparent url('../Images/MenuDropdown.png') no-repeat scroll center center;
}

.cssTabStrip_Link_LevelOne:hover .tsCenterCell .cssParentDropArrow,
.cssTabStrip_Link_LevelOne:focus .tsCenterCell .cssParentDropArrow,
.cssTabStrip_Link_LevelOne:active .tsCenterCell .cssParentDropArrow,
.cssTabStrip_Link_LevelOne:hover .tsFirstTabCenter .cssParentDropArrow,
.cssTabStrip_Link_LevelOne:focus .tsFirstTabCenter .cssParentDropArrow,
.cssTabStrip_Link_LevelOne:active .tsFirstTabCenter .cssParentDropArrow,
.tsItem:hover .tsCenterCell .cssParentDropArrow,
.tsItem:focus .tsCenterCell .cssParentDropArrow,
.tsItem:active .tsCenterCell .cssParentDropArrow,
.tsItemSelected .tsCenterCellSelected .cssParentDropArrow
{
	background-image: url('../Images/Tabs/White_Selected_MenuDropdown.png');
}

.cssTabStrip_On .cssParentDropArrow
{
	background-image: url('../Images/Tabs/White_Selected_MenuDropdown.png');
}

.cssTabStrip_Link_LevelOne:hover .cssTabCenter .cssParentDropArrow,
.cssTabStrip_Link_LevelOne:focus .cssTabCenter .cssParentDropArrow,
.cssTabStrip_Link_LevelOne:active .cssTabCenter .cssParentDropArrow,
.cssTabStrip_Link_LevelOne:hover .cssFirstTabCenter .cssParentDropArrow,
.cssTabStrip_Link_LevelOne:focus .cssFirstTabCenter .cssParentDropArrow,
.cssTabStrip_Link_LevelOne:active .cssFirstTabCenter .cssParentDropArrow,
.tsItemFirst:hover .tsCenterCellFirst .cssParentDropArrow,
.tsItemFirst:focus .tsCenterCellFirst .cssParentDropArrow,
.tsItemFirst:active .tsCenterCellFirst .cssParentDropArrow
{
	background-image: url('../Images/Tabs/White_Selected_MenuDropdown.png');
}

.tabstrip .tsCenterCell, .tabstrip .tsCenterCellFirst {
	height: 13px;
	line-height: 13px;
}

/* Tab Flyouts */
.cssTabStrip_Link_LevelTwo {
	color:$tabFlyoutColor;
	background-color:$tabFlyoutBGColor;
	&:hover,
	&:focus,
	&:active {
		color:$tabFlyoutColor !important;
		background-color:$tabFlyoutBGColorHover !important;
		text-decoration:none;
	}
}

.cssTabStrip_Flyout > ul li,
.cssTabStrip_Link_LevelTwo.cssTabStrip_On {
	border-color:$tabFlyoutBGColor;
	background-color:$tabFlyoutBGColor;
	color:$tabFlyoutColor;
	&:hover,
	&:focus,
	&:active {
		.cssTabStrip_Link_LevelTwo {
			color:$tabFlyoutColor !important;
			background-color:$tabFlyoutBGColorHover !important;
		}
	}
}

.cssTabStrip_Link_LevelTwo.cssTabStrip_On {
	font-weight:600;
}

.cssTabStrip_Flyout {
	border-color:$tabFlyoutBGColor;
	box-shadow: 1px 1px 10px 3px $tabFlyoutBoxShadowColor;
}

.tabstrip span.cssTabStrip_Flyout {	// push flyout down
	margin: 18px 0 0;
}

/* Sub Tab Strip */

#kCuraSubTabStrip_subTabList {
	background-color: $inactiveSubTabBGColor;
	li {
		vertical-align:top;
		padding-top:0px;
		height:26px;
		a {
			border-left:none;
			padding-top:4px;
		}
	}
}

.subtabstrip ul li.divider a, .subTab {	// hide tab divider
	border-left: 1px solid $inactiveSubTabBGColor;
}

.subtabstrip ul li.divider a.subTabSelected,
.subtabstrip ul li a.subTabSelected {	// Selected sub tab styles
	//padding-top: 4px !important;
	color:$activeSubTabColor;
	background-color:$activeSubTabBGColor;
	//font-weight:600;						// 700 looked too clunky
	text-shadow: 0 0 $activeSubTabColor;						// text shadow to give the bold effect saving the bad rendering of open sans
	&:hover {	// remove hover styling from selected subtab
		color:$activeSubTabColor !important; // needs !important
		background-color:$activeSubTabBGColor !important; // needs !important
	}
	&:focus,
	&:active {
		color:$activeSubTabColor !important;
		background-color:$inactiveTabBGColorHover !important;
	}
}

.subtabstrip ul li.divider a,
.subtabstrip ul li a.subTab {	// hover styling
	background-color:$inactiveSubTabBGColor;
	margin-left: -1px;
	height:25px;
	&:hover,
	&:focus,
	&:active {
		color:$activeSubTabColor !important; // needs !important
		background-color:$inactiveSubTabBGColorHover !important; // needs !important
	}
}

.subtabstrip ul li.divider,
.subtabstrip ul li {		// move sub tab elements upwards
	margin-top:0px;
}

.subtabstrip ul li a {
	color:$inactiveSubTabColor;
	font-weight:400;
	height:25px;
}

.subtabstrip ul li, .subTab {
	vertical-align:top !important;
}
