
/* pivot */
.pivotActionBar
{
	padding: 5px 10px 18px 5px;
	border-bottom: black 1px solid;
	background-color: $defaultWhite;
}

.pivotChartContainer {
	background: $grayLight;
}

.pivot-button-container {
	position: relative;
	top: 9px;
	width: 210px;
}

.chartOptionFlyout {
	left: 35px !important;
	top: 3px;
	background-color: $grayLight;
	border: 1px solid $flyoutBorderColor;
	box-shadow: 0 6px 10px 0 #acacac;
}

.chartOptionFlyout:after,
.chartOptionFlyout:before {
	top: 3px;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	left: -15px !important;
	border-width: 7px;
	position: absolute;
	pointer-events: none;
	border-color: rgba(0, 0, 0, 0);
}

.chartOptionFlyout:before {
	border-right-color: $flyoutBorderColor;
}

.chartOptionFlyout:after {	
	border-right-color: $grayLight;
	left: -13px !important;
}

#pivotOptionsCentralCell{
	border-right: 1px dotted $pivotOptionsCellBorderColor !important;
	border-left: 1px dotted $pivotOptionsCellBorderColor !important;
}

.chart-actionbar .chartOptionFlyout table td {
	padding: 0 15px;
	text-align: left;
}

.chartOptionFlyout>table{
	margin-top: 8px;
	margin-bottom: 8px;
}

.chartOptionFlyout .switch-series+span{
	position: absolute;
	bottom: 14px;
	right: 15px;
}

.chartOptionFlyout .switch-series+span a{
	padding: 2px 17px;
}

.chartOptionFlyout label:not([class]){
	padding-left: 4px;
}

#pivotOptionsRightCell td{
	padding-bottom: 6px;
	padding-right: 25px;
}

#pivotOptionsLeftCell .ListTemplateHeaderText{
	width: 65px;
	text-align: right;
}

.pivotpager{
	margin-top: -5px;
}

.pivotActionBar > div{
	padding: 0 !important;
}

#pivotOptionsLeftCell select{
	height: 20px !important;
	padding: 0 !important;
	line-height: 20px !important;
	margin-bottom: 2px;
	margin-top: 2px;
	width: 100px;
}

.pivotActionBar select{
	height: 23px !important;
	padding: 0 !important;
	line-height: 23px !important;
	margin-bottom: 1px;
	margin-top: 2px;
	width: 160px;
}

.pivotActionBar>span:nth-child(1) a:nth-child(1){
	width: 24px;
	padding: 2px 25px;
	margin: 0;
	margin-right: 4px;
}

.pivotActionBar>span:nth-child(1) a:nth-child(2){
	width: 40px;
	padding: 2px 19px;
	margin: 0;
}

.pivotActionBar>span:nth-child(2) a{
	padding: 2px 8px;
	height: 18px !important;
	line-height: 18px !important;
}

.pivotActionBar>span:nth-child(2){
	margin-top: 1px;
	margin-left: 2px;
	margin-right: 10px;
}

.pivotActionBar>span:nth-child(3) select{
	margin-top: 0;
	margin-bottom: 2px;
	width: 120px;
}

.pivotActionBar>span:nth-child(3) td:nth-child(2) select{
	width: 80px;
}

.chart-actionbar .option-choices td{
	padding: 3px 6px !important;
}

.chart-actionbar{
	margin: 0;
	height: 100%;
	width: 26px;
	background-color: $pivotOptionsLeftBarColor;
}

.chart-actionbar>span{
	padding: 5px 5px 0 5px;
}

.chart-actionbar>input{
	padding: 0 5px;
	margin: 0 !important;
}

.pivotChartContainer{
	position: relative;
}

.pivotChartContainer span:nth-child(2) img{
	box-sizing: border-box;
}

.pivotActionBar span{
	border-left: none !important;
	border-right: none !important;
}

.PivotMassActionBar,
.pivotActionBar,
.pivotpager,
.ArtifactsViewTemplateHeader.pivotcontainer,
.errorMessageLightBlueBackGround.pivotErrorMessage{
	background-color: $grayLight;
	border-color:$defaultBorderColor !important;
}

.pivotActionBar > div{
	padding-left: 200px !important;
	padding-right: 300px !important;
	box-sizing: border-box;
}

.pivotpager .rightAligned input{
	padding-right: 3px;
}

.pivotmessagebar[id$="_pivotmessagebar"] {
	background-color:$pivotMessageBarBGColor;
	color:$pivotMessageBarColor !important;
}

.pivotpager a[disabled]:hover {
	color:$pivotPagerDisabled;
}

.PivotMassActionBar>span:first-child{
	margin-right: 20px;
}

.PivotMassActionBar>span:nth-child(2){
	margin-left: 2px;
}

.pivotpager .rightAligned{
	margin-right: 18px;
}

.pivotpager > span:nth-child(2){
	margin-left: 6px;
}

.pivot-button-container input{
	padding-right: 10px;
	outline: 0;
}

/*pivot end */