.upDownButtonsTable {
	td {
		padding-top:0;
		padding-bottom:3px;
	}
	.fixedWidthButton2 {
		background: none;
		width: 16px;
		height: 16px;
		a:hover, &:hover {
			background: none;
		}
		span {
			width: 16px;
			height: 16px;
		}
		span.moveUpButton {
			background: url(/relativity/Images/moveup.png) no-repeat;
			&:hover {
				background: url(/relativity/Images/moveup_hover.png) no-repeat;
			}
		}
		span.moveDownButton {
			background: url(/relativity/Images/movedown.png) no-repeat;
			&:hover {
				background: url(/relativity/Images/movedown_hover.png) no-repeat;
			}
		}
		span.moveToTopButton {
			background: url(/relativity/Images/movetotop.png) no-repeat;
			&:hover {
				background: url(/relativity/Images/movetotop_hover.png) no-repeat;
			}
		}
		span.moveToBottomButton {
			background: url(/relativity/Images/movetobottom.png) no-repeat;
			&:hover {
				background: url(/relativity/Images/movetobottom_hover.png) no-repeat;
			}
		}

		&:focus{
			span.moveUpButton {
					background: url(/relativity/Images/moveup_hover.png) no-repeat;
			}
			span.moveDownButton {
					background: url(/relativity/Images/movedown_hover.png) no-repeat;
			}
			span.moveToTopButton {
					background: url(/relativity/Images/movetotop_hover.png) no-repeat;
			}
			span.moveToBottomButton {
					background: url(/relativity/Images/movetobottom_hover.png) no-repeat;
			}
		}
	}
}

.buttermilkTwoListSelect {
	.fixedWidthButton1 {
		width:16px;
		margin-left:8px;
		margin-right:8px;
		margin-bottom:8px;
		span {
			width: 16px;
			height: 16px;
		}
		span.moveAllToTwoButton {
			background: url(/relativity/Images/move_all_to_two.png) no-repeat;
			&:hover {
				background: url(/relativity/Images/move_all_to_two_hover.png) no-repeat;
			}
		}
		span.moveSelectedToTwoButton {
			background: url(/relativity/Images/move_selected_to_two.png) no-repeat;
			&:hover {
				background: url(/relativity/Images/move_selected_to_two_hover.png) no-repeat;
			}
		}
		span.moveSelectedToOneButton {
			background: url(/relativity/Images/move_selected_to_one.png) no-repeat;
			&:hover {
				background: url(/relativity/Images/move_selected_to_one_hover.png) no-repeat;
			}
		}
		span.moveAllToOneButton {
			background: url(/relativity/Images/move_all_to_one.png) no-repeat;
			&:hover {
				background: url(/relativity/Images/move_all_to_one_hover.png) no-repeat;
			}
		}
		&:focus{
			span.moveAllToTwoButton {
					background: url(/relativity/Images/move_all_to_two_hover.png) no-repeat;
			}
			span.moveSelectedToTwoButton {
					background: url(/relativity/Images/move_selected_to_two_hover.png) no-repeat;
			}
			span.moveSelectedToOneButton {
					background: url(/relativity/Images/move_selected_to_one_hover.png) no-repeat;
			}
			span.moveAllToOneButton {
					background: url(/relativity/Images/move_all_to_one_hover.png) no-repeat;
			}
		}

	}
}

body:after
{
content: url(/relativity/Images/moveup_hover.png)
		 url(/relativity/Images/movedown_hover.png)
		 url(/relativity/Images/movetotop_hover.png)
		 url(/relativity/Images/movetobottom_hover.png)
		 url(/relativity/Images/move_all_to_two_hover.png) 
		 url(/relativity/Images/move_selected_to_two_hover.png)
		 url(/relativity/Images/move_selected_to_one_hover.png)
		 url(/relativity/Images/move_all_to_one_hover.png);
display: none;
}

.viewsEditPage .viewInformation {
	.fieldTable{
		max-width: 600px;
	}
	input[type=text][id$=__name_textBox_textBox]{
		max-width: 460px;
	}
}