
/*Controls*/


.graphicCheckbox {
	width: 13px;
	height: 13px;
	background: url(/Relativity/Images/ToggleControls/Unchecked_Box.png) no-repeat;
	display: inline-block;
	position: relative;
	top: 2px;
	&.on {
		background: url(/Relativity/Images/ToggleControls/Checked_Box.png) no-repeat;
	}

}

		

.graphicRadio {
	width: 14px;
	height: 14px;
	background: url(/Relativity/Images/ToggleControls/Radio_Off.png) no-repeat;
	display: inline-block;
	position: relative;
	top: 2px;
	&.on {
		background: url(/Relativity/Images/ToggleControls/Radio_On.png) no-repeat;
	}
}

.defaultInput{
	//background-color: $defaultWhite;
	border: 1px solid $defaultInputBorderColor;
	border-radius: 3px;
	color: $blueDark;
	padding:2px 2px 2px 4px;
	line-height:1rem;
	font-family: $defaultFontFamily;
	font-size:$defaultFontSize;
	min-height:20px;
	&:disabled {
		background-color: $defaultDisabledBGColor;
		border-color: $defaultDisabledBorderColor;
	}
	&:focus {
		border-color:$focusBorderColor;
		outline:none;
	}

}
		
input[type="text"], input[type="password"], textarea {
	@extend .defaultInput;
	.textualPaginationControls &{
		padding:0;
	}
	tr.filters-row & {
		min-height:24px;
	}
}



select{
	//height:100% !important;
	vertical-align:middle;
	//padding:2px;
	
	border-radius:3px;
	border: 1px solid $defaultInputBorderColor;
	font-family: $defaultFont;
	background-color:$defaultWhiteAbsolute;

	&:focus {
		border-color:$focusBorderColor;
		outline:none;
	}
}
select:not([multiple]):not([size="4"]){
	height:24px !important;
	line-height:24px !important;

}

.helpIconLink{
	background: url(/Relativity/images/help_icon.png) no-repeat;
	width:16px;
	height:16px;
	border:0px none transparent;
}

.dynamicEditFieldValue span[id$="_description"],
.editFieldValue span[id$="_description"],
.dynamicEditFieldValue .artifact-picker-description,
.editFieldValue .artifact-picker-description,
.dynamicEditFieldValue .PopupPickerDescriptionSpan,
.editFieldValue .PopupPickerDescriptionSpan,
.massEditFieldValueProfile span[id$="_description"] {
	background-color: $grayLight !important;
	border: none !important;
	border-radius: 0px !important;
	border-bottom: 1px solid $defaultBorderColor !important;
	padding-right: 10px !important;
	margin-right: 10px !important;
}

.viewCriteriaPickerTable .description,
#displayLibraryApplicationId {
	background: transparent !important;
	border: none !important;
	border-radius: 0px !important;
	border-bottom: 1px solid $defaultBorderColor !important;
	padding-right: 10px !important;
	margin-right: 10px !important;
}

.PopupPickerDescriptionSpan {
	background-color: $grayLight !important;
}

.dxeButtonEdit input{
	padding: 0;
}

.jstree-classic.jstree-focused {
	background: $cardBGColor !important;
}

//worried about the first 2 so commenting them out for now
.dynamicEditFieldValue span[id$="_description"]:empty:before, 
.editFieldValue span[id$="_description"]:empty:before,
.dynamicEditFieldValue .artifact-picker-description:empty:before, 
.editFieldValue .artifact-picker-description:empty:before, 
.dynamicEditFieldValue .PopupPickerDescriptionSpan:empty:before, 
.editFieldValue .PopupPickerDescriptionSpan:empty:before, 
.singleObjectPickerDisplayEmpty:before,
.massEditFieldValueProfile span[id$="_description"]:empty:before,{
	content: "Select...";
	color: $blueLink;
}