.massEditMain {
	.massEditTable {
		@extend .cardTable;
		padding: 5px 5px 10px;
		margin: 15px;
		
		-ms-border-radius: 5px;
		border-radius: 5px;
		width: 95%;
		max-width: 1200px;
		margin-left: auto;
		margin-right: auto;
		border-collapse: separate;
		&:empty {
			padding:0;
			border-width:0;
		}
		.viewSectionTitle {
			color: $cardSectionHeaderFont;
			font-weight: 600;
			font-size: 13px;
			margin-top: 5px;
			margin-bottom: 5px;
			background: transparent;
		}
		.editTableColumn > table {
			border-spacing:0 4px;
		}
		.viewFieldNameProfile,
		.massEditFieldNameProfile {
			word-break:break-word;
		}
		.massEditFieldValueProfile .richTextValue {
			border-radius:3px;
		}
	}
	.editTableColumn > table > tbody > tr > td {
		border: none;
		&:last-of-type {
			border: none;
		}
	}
	.massEditTable table {
		width: 100%;
		td {
			&.viewFieldNameProfile, &.massEditFieldNameProfile {
				width: 180px;
				min-width: 180px;
				font-weight: bold;
				max-width: 200px;
				color: $cardLabelFont;
				background-color: transparent;
				font-size: 12px;
				white-space:normal;
				word-wrap: break-word;
				word-break: normal;
				padding-top: 4px;
				img {
					max-width:98%;
					height:auto;
				}
			}
			&.viewFieldValueProfile {
				background-color: $defaultBGColor;
				padding-right: 30px;
			}
		}
	}
}

td.massEditFieldValueProfile {
	background-color: $defaultBGColor;
	padding-right: 30px;
	td {
		font-size: 12px;
	}
	.richTextValueTable .pickButton {
		top: 0 !important;
	}
}

.massEditTable .editTableColumn td:first-child {
	min-width: 25px;
	padding-left: 7px;
}

.massEditTable .editTableColumn>table>tbody>tr>td>span.massEditFieldCheckbox{
	margin-top: 5px;
	height: 17px;
	input{
		vertical-align: top;
		margin-top: 0;
	}
}

.massEditTable .MultichoiceCheckbox {
	input{
	  position: relative;
	  vertical-align: middle;
	  bottom: 1px;
	}
}

.massEditTable .editTableColumn .viewFieldNameProfile {
	padding: 2px 5px 2px 5px;
}

.massEditRendererContainer #dynamicViewRenderer_templateContainer {
	padding-left: 10px;
	padding-right: 10px;
}

.massEditTable .viewSectionTitle {
	padding-left: 10px;
}

.massEditRendererContainer+.popupArtifactActionBar{
	display: none;
}

/*MASS PRODUCE*/

.massProduceContainer td.itemListHead+th {
	border-right: 1px solid $defaultBorderColor !important;
}

/*MASS REPLACE*/

.massReplacePopup .artifactPopupActionBarFull{
	border-bottom: 1px solid $defaultBorderColor;
	border-top: 0;
}

.massReplacePopup .fieldTable{
	margin-bottom: 5px !important;
}

.massReplacePopup .editTable{
	border: solid 1px $defaultBorderColor !important;
}

.massReplacePopup #_kCuraScrollingDiv {
	padding: 0 10px;
	width: 95% !important;
}

.contentAnalyst #_nameWrapper,
.contentAnalyst #_clusterWrapper,
.contentAnalyst #_indexWrapper{
	margin-left: 1px;
}