/* Flyouts */
/* 
    rebranding the flyouts in the menu bar
    adds arrow pointers
*/

/* Base flyout styles */
.quickNavFlyout,
.userGreetingFlyout,
.historyFlyout,
.alertFlyout {
	top: 18px;
	position: relative;
	background: $flyoutBGColor;
	border: 1px solid $flyoutBorderColor;
	margin-top: 10px;
	-ms-border-radius: 3px;
	border-radius: 3px;
	box-shadow: 0 6px 10px 0 $flyoutBoxShadowColor;
}

.quickNavFlyout:after,
.userGreetingFlyout:after,
.historyFlyout:after,
.alertFlyout:after,
.quickNavFlyout:before,
.userGreetingFlyout:before,
.historyFlyout:before,
.alertFlyout:before {
	bottom: 100%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
	border-color: rgba(0, 0, 0, 0);
}

.quickNavFlyout:after,
.userGreetingFlyout:after,
.historyFlyout:after,
.alertFlyout:after {
	border-bottom-color: $flyoutBGColor;
	border-width: 10px;
	margin-right: -10px;
}

.quickNavFlyout:before,
.userGreetingFlyout:before,
.historyFlyout:before,
.alertFlyout:before {
	border-bottom-color: $flyoutBorderColor;
	border-width: 11px;
	margin-right: -11px;
}

/* Reposition flyouts */
.userGreetingFlyout {
	margin-right:-8px !important; // needs !important
}

.historyFlyout {
	margin-right:140px !important; // needs !important
}

/* Align arrows */
.quickNavFlyout {
	&:after, &:before {
		right:4%;
	}
}

.userGreetingFlyout {
	&:after, &:before {
		right:14px;
	}
}

.alertFlyout {
	&:after, &:before {
		right:18px;
	}
}

.historyFlyout {
	&:after, &:before {
		right:26px;
	}
}

/* Flyout labels */
span[class$="_favoriteGroup_Header"],
span[class$="_historyGroup_Header"],
.quickNavResultGroup {
	&:not(:first-child) {
		margin-top:10px;
	}
	color:$flyoutLabelColor !important; // needs !important
	text-transform: uppercase;
	font-weight:400 !important; // needs !important
	margin-bottom:0;
}

/* Flyout menu items / links */
.buttermilk-flyout-menu-item {
	color:$flyoutMenuItemColor !important; // needs important
	//text-decoration:underline !important; // needs important
	border-width:1px 0 1px 0 !important; // needs important
	border-style:solid;
	border-color:$flyoutBGColor;
	&:hover,
	&:focus {
		text-decoration:underline !important;
	}
	&:focus {
		outline:none;
	}
}
.userGreetingFlyout,
.historyRowItem {
	a {
		@extend .buttermilk-flyout-menu-item;
		&:hover,
		&:focus {
			color:$flyoutMenuItemColorHover;
			background-color:$flyoutMenuItemBGColorHover;
			border-top: 1px solid $flyoutBGColor;
			border-bottom: 1px solid $flyoutBGColor;
			border-left:0;
		}
        &#UserFlyoutSwitchUILink {
            cursor:pointer;
            border-bottom: 0;
        }
	}
}

.quickNavResultItem a {
	@extend .buttermilk-flyout-menu-item;
}

.quickNavResultItem.ui-menu-item {
	float:none;
}

.quickNavAutoCompleteMenu br {
	display:none;
}

.quickNavFlyout {
	.ui-state-hover, .ui-widget-content .ui-state-hover, .ui-widget-header .ui-state-hover, .ui-state-focus, .ui-widget-content .ui-state-focus, .ui-widget-header .ui-state-focus {
		color:$flyoutMenuItemColorHover;
		background-color:$flyoutMenuItemBGColorHover;
		border-top: 1px solid $flyoutBGColor;
		border-bottom: 1px solid $flyoutBGColor;
		border-left:0;
	}
}

.userGreetingFlyout hr,
.quickNavFlyout hr {
	background-color:$flyoutBorderColor;
	color:$flyoutBorderColor;
	border-width:0;
	height:1px;
    margin-top:6px;
}

/* Alert flyout */
.alertFlyout {
	color:$flyoutAlertTextColor;
	a {
		color:$flyoutAlertLinkColor;
		//text-decoration:underline;
		&:hover {
			color:$flyoutAlertLinkColorHover;
			background-color: rgba(0, 0, 0, 0);
			text-decoration:underline;
		}
	}
}

.RelativityHeaderAlert {
	text-decoration: none;
	color:$flyoutAlertLabelColor;
	font-weight:700;
	&:after {
		content: "S";
	}
}

/* QuickNav flyout */
.quickNavAutoCompleteMenu {		// Override jQuery UI auto-results styling to match new BG color
	background: none !important;
}

.quickNavTextbox {		// QuickNav text input
	border: 1px solid $flyoutInputBorderColor;
	-ms-border-radius: 3px;
	border-radius: 3px;
	color:$flyoutInputColor;
}

.visibilityFlyout {
	width: 411px;
	background: $flyoutBGColor;
}