
/*Buttons*/


/*basic button*/
.buttermilk-button{
	text-decoration: none;
	background-color: $buttonBGColor !important;
	background: linear-gradient($buttonGradientStart, $buttonGradientEnd);
	border-color: $buttonBorderColor;
	border-style:solid;
	border-width:1px;
	border-radius: 3px;
	padding-top:3px;
	padding-bottom:3px;
	padding-left:8px;
	padding-right:8px;
	vertical-align:middle;
	height:16px;
	line-height:16px;
	font-family:$defaultFontFamily;
	font-size:$defaultButtonFontSize;
	font-weight:normal;
	box-sizing:content-box;
	//margin: 4px 15px 4px 15px;
	//box-shadow: 0px 1px 1px #397eb8 inset;
	color: $defaultWhite !important;
	&:hover,
	&:focus{
		border-style:solid;
		border-width:1px;
		color:$defaultWhite;
		background-color:$buttonBGColorHover !important;
		background: linear-gradient($buttonGradientStartHover, $buttonGradientEndHover);
		border-color: $buttonBGColorHover !important;
		text-decoration:none !important;
	}
}

.buttermilk-button-positive{
	@extend .buttermilk-button;
}

.cviz-button{
	@extend .buttermilk-button;
  display: inline-block;
  width: 215px;
  height: 24px;
  line-height: 24px;
  font-weight: 600;
  font-size: 12px;
  margin-left: 6px;
  text-align: center;
  margin-top: 4px;
	&:hover,
	&:focus{
		text-decoration: none;
	}
}

.cviz-button-disabled{
	@extend .cviz-button;
	background-color: $buttonBGColorDisabled !important;
	background: linear-gradient($buttonBGColorDisabled, $buttonBGColorDisabled);
	border-color: $buttonBGColorDisabled  !important;
	color: $buttonFontColorDisabled !important;

	&:hover, 
	&:focus{
		background-color: $buttonBGColorDisabled !important;
		background: linear-gradient($buttonBGColorDisabled, $buttonBGColorDisabled);
		border-color: $buttonBGColorDisabled !important;
		color: $buttonFontColorDisabled;
	}
}


.buttermilk-button-disabled{
	@extend .buttermilk-button;
	background-color: $buttonBGColorDisabled !important;
	background: linear-gradient($buttonBGColorDisabled, $buttonBGColorDisabled);
	border-color: $buttonBGColorDisabled  !important;
	color: $buttonFontColorDisabled !important;

	&:hover, 
	&:focus{
		background-color: $buttonBGColorDisabled !important;
		background: linear-gradient($buttonBGColorDisabled, $buttonBGColorDisabled);
		border-color: $buttonBGColorDisabled !important;
		color: $buttonFontColorDisabled;
	}
}

.buttermilk-button-destructive{
	@extend .buttermilk-button;
	background-color:$buttonBGColorDestructive;
	background: linear-gradient($buttonGradientStartDestructive,$buttonGradientEndDestructive);
	border-color: $buttonBorderColorDestructive;

	&:hover, 
	&:focus{
		background-color: $buttonBGColorDestructive;
		background: linear-gradient($buttonGradientStartDestructiveHover, $buttonGradientEndDestructiveHover);
		border-color: $buttonBorderColorDestructive !important;
	}
}

.buttermilk-button-console {
	height:22px;
	line-height:22px;
	font-size:$defaultButtonFontSize;
	font-weight:600;
	margin-left:10px;
	margin-right:10px;
	&:hover {
		text-decoration:none;
	}
}

