/* Modals begin */

.ui-widget-header {
	background:$modalBGColor;
	color:$modalColor;
	font-weight:600;
}

.ui-dialog .ui-dialog-title {
	padding-top:8px;
}

.ui-widget-content {
	background:$modalBGColor;
	border-width:0;
}

.ui-dialog .ui-dialog-buttonpane {
	border-top-width:0;
}

.ui_tpicker_hour_slider,
.ui_tpicker_minute_slider {
	background:#ddd;
	> .ui-slider-handle {
		background:$blueBright;
		border-color:$blueBright;
		&:hover {
			background:#e58e1a;
			border-color:#e58e1a;
		}
	}
}

.ui-datepicker .ui-datepicker-buttonpane button {
	@extend .buttermilk-button-positive;
}

.ui-button {
	@extend .buttermilk-button-positive;
	height:24px !important;
	background-color: $buttonBGColor !important;
	background: linear-gradient($buttonGradientStart, $buttonGradientEnd) !important;
	border-color: $buttonBorderColor;
	border-width:1px !important;
	&.ui-state-disabled {
		@extend .buttermilk-button-disabled;
		background-color: $buttonBGColorDisabled !important;
		background: linear-gradient($buttonBGColorDisabled, $buttonBGColorDisabled) !important;
		border-color: $buttonBGColorDisabled;
		border-width:1px !important;
		opacity:1;
	}
	&:hover,
	&:focus {
		&.ui-state-disabled {
			background-color: $buttonBGColorDisabled !important;
			background: linear-gradient($buttonBGColorDisabled, $buttonBGColorDisabled) !important;
			border-color: $buttonBGColorDisabled !important;
		}
		background-color:$buttonBGColorHover !important;
		background: linear-gradient($buttonGradientStartHover, $buttonGradientEndHover) !important;
		border-color: $buttonBGColorHover !important;
		border-width:1px !important;
		outline:none;
	}
	&.ui-state-active {
		border:1px solid $buttonBorderColor;
	}
}

.helpTooltip {
	background:$helpTooltipBGColor;
	color:$helpTooltipColor;
	border-color:$helpTooltipBGColor;
	-webkit-box-shadow: 2px 2px 1px $helpTooltipBoxShadowColor;
	-ms-box-shadow: 2px 2px 1px $helpTooltipBoxShadowColor;
	box-shadow: 2px 2px 1px $helpTooltipBoxShadowColor;
	-ms-border-radius: 3px;
	border-radius: 3px;
}

.helpTooltip a {
	color:$helpTooltipColor !important;
	font-style: italic;
	&:hover {
		text-decoration: underline !important;
	}
}

.ui-datepicker .ui-datepicker-header {
	background:$blueBright !important;
}

.ui-datepicker-calendar td:first-child .ui-state-active {
	width:auto !important;
}

.ui-datepicker-calendar .ui-state-active, .ui-datepicker-calendar .ui-datepicker-today .ui-state-active {
	background:$blueBright !important;
}

.dialogArtifactActionBar {
	background-color:$modalBGColor !important;
}

/*
This breaks datepicker modal headers
.ui-widget-header:not(.ui-progressbar-value) {
	background:$modalBGColor !important;
}*/

.floatingDivHeader {
	background-color:$blueBright !important;
	color:$defaultWhite;
	font-weight:600;
}

.ui-widget-content a {
	color: $blueLink;
}

.ui-widget-header .ui-dialog-titlebar-close .ui-icon {
	background-image: url(/Relativity/images/Modal_Close.png);
	background-position: 0 0;
	top: 0px;
	right: -10px;
}

div:not(.cornerclosebutton) .ui-widget-header .ui-dialog-titlebar-close {
	top: 0px;
	right: -10px;
}

.ui-widget-header .ui-dialog-titlebar-close {
	background: transparent !important;
}

.ui-dialog.ui-widget-content {
	overflow: visible;
}

.ui-dialog-titlebar.ui-widget-header,
.ui-dialog.ui-widget-content{
	border-radius: 3px;
}

.ui-datepicker {
	.ui-datepicker-prev,
	.ui-datepicker-next {
		margin:0 !important;
		padding:3px 2px 3px 2px !important;
		top:0;
	}
	.ui-datepicker-prev-hover {
		margin:0 !important;
		padding:2px 0px 2px 2px !important;
		top:0;
	}
	.ui-datepicker-next-hover {
		margin:0 !important;
		padding:2px 2px 2px 1px !important;
		top:0;
	}
	.ui-datepicker-prev,
	.ui-datepicker-prev-hover {
		left:0;
	}
	.ui-datepicker-next,
	.ui-datepicker-next-hover {
		right:0;
	}
}

.bmImportDialog {
	background:none;

	.ImportModalDialog {
		background-color:#e6e6e6;
		color:$importApplicationModalColor;
		h3 {
			font-size:24px;
			font-weight:400;
		}
	}
}

.sendMessage .popupArtifactActionBar{
	border: 0;
}

.aboutPopup {
	margin-top:-33px;
	left:-16px;
	.AboutBoxLowerPanel {
		margin-left:12px;
	}
}

.dialogDiv.spinningModal {
	.spinningModalIcon,
	.spinningModalText {
		text-align:center;
	}
}

.loadingSpanAbsolute {
	top: 8px;
	position: absolute;
	left: 80px;
}

#termswindowcontent #popupText{
	font-weight: normal;
}

#termswindowcontent .floatingDivMessage img {
	margin-top: -3px;
}

.instanceDetailsDialog {
	border: solid 1px $defaultBorderColor;
}

/* Modals end */