/*artifact template header start*/

.buttermilk-actionbar {
	background-color: $defaultBGColor;
	border-bottom-color:$defaultBorderColor !important;
}

.action-bar-bg {
	background-color: $defaultBGColor;
}

.actionbartop,
.actionbar,
.ArtifactsViewTemplateHeader,
.artifactActionBar,
.artifactPopupActionBar,
.genericArtifactTopActionBar,
.genericArtifactTopActionBar .documentNavigatorNonButtonCell,
.genericArtifactTopActionBar .documentNavigatorButtonCell,
.dynamicActionBarTop,
.popupArtifactActionBar,
.singlePopupArtifactActionBar {
	@extend .buttermilk-actionbar;
}

.actionbar {
	height:24px;
	padding-bottom:3px;
}

.LicenseScreenErrorMessage {
	background-color:$defaultMessageBarBGColor;
	color:$defaultMessageBarColor;
}

.popupArtifactActionBar {
	padding-top:3px;
	padding-bottom:3px;
}

.regularTitle { // only come across this on custom pages ... don't think this should have the dark blue background
	padding-top:7px;
	color:$blueDark;
	font-weight:600;
	border-bottom: 1px solid $defaultBorderColor;
	@extend .buttermilk-actionbar;
}

.artifactActionBarTopBorder,
.singlePopupArtifactActionBar
{
	border-top-color: $defaultBorderColor;
}

.dynamicTemplateErrorMessageWidth100,
.errorMessageLightBlueBackGroundCenteredWidth100,
.editTemplateErrorMessage,
.newErrorMessage,
.viewTemplateErrorMessage {
	background-color: $defaultBGColor;
}
/*
.artifactActionBar .PersonalDropdownPositioning:not(.PersonalDropdownPositioningNotAbsolute){
	position: absolute;
	left: 5px;	
}
*/
.PersonalDropdownPositioning{
margin-right: -15%;
}

.PersonalDropdownPositioning select{
	max-width: 220px !important;
}

.grey-header {
	border-bottom:1px solid $defaultBorderColor;
}

.popupArtifactBottomActionBar {
	background-color:$defaultBGColor;
	border-top-color:$defaultBorderColor;
}

.batch-edit #_userContainer {
	select#_userdropdown {
		margin-bottom:6px;
	}
	#_selectMeButton {
		margin-left:5px;
	}
}

.massActionBar .listFooter {
	border-top:1px solid $defaultBorderColor;
}

/*artifact template header end*/
