/* Card Layout */
/*
    Used on the details layouts
    replaces old table based forms
	Note: these are new classes. The HTML had to be modified to obtain this new look
*/


.cardSectionHeader{
	color:$cardSectionHeaderFont;
	//font-weight: normal;
	font-weight:600;
	font-size: $defaultSectionHeaderFontSize;
	margin-top: 5px;
	margin-bottom: 5px;
	padding-left: 15px;
	//padding-left:0;
}
.editTableColumn .editSectionTitle,
.viewTableColumn .viewSectionTitle,
.editTableColumn .viewSectionTitle{
	padding-left:0 !important;
}

.cardSection > table.viewTable > tbody > tr > td.viewTableColumn > div.viewSectionTitle,
.cardSection > table.editTable > tbody > tr > td.editTableColumn > div.editSectionTitle{
	padding-left:15px !important;
}

#_main .editTable .EditTableColumn .viewSectionTitle{
	padding-left: 9px;
}

.customCardLabel{
	font-weight:bold;
}

.cardLabel{
	color: $cardLabelFont;
	background-color: transparent;
	font-size: $defaultFontSize;
	font-weight:bold;
	padding-top:2px;
	padding-bottom:2px;
	border:none;
}

.cardValue{
	background-color: $cardBGColor;
	border-bottom-style: none;
	border-left-style: none;
	padding-left: 5px;
	padding-top:2px;
	padding-bottom:2px;
	font-size: $defaultFontSize;
	font-weight: normal;
}

.cardViewValue{
	background-color: $cardBGColor;
	color:$cardViewValueFont;
	border-bottom-style: solid !important;
	border-bottom-width: 1px !important;
	border-bottom-color: $cardBorderColor !important;
	border-left-style: none;
	padding-left: 5px;
	padding-top:2px;
	padding-bottom:2px;
	font-size: $defaultFontSize;
	font-weight: normal;
}

.cardRequiredLabel{
	@extend .cardLabel;
	border-style: none solid none none !important;
	border-width: $fieldLableRightBorderWidth !important;
	border-top-color: transparent;
	border-right-color: $cardRequiredBorderRightColor !important;
	border-bottom-color: transparent;
	border-left-color: transparent;
}

.cardNotRequiredLabel{
	@extend .cardLabel;
	border-style: none solid none none !important;
	border-width: $fieldLableRightBorderWidth;
	border-top-color: transparent;
	border-right-color: transparent !important;
	border-bottom-color: transparent;
	border-left-color: transparent;
}

.cardTable{
	background-color: $cardBGColor;
	border: thin solid $cardSectionBorder;
	-webkit-box-shadow: 2px 2px 1px $defaultBoxShadow;
	-ms-box-shadow: 2px 2px 1px $defaultBoxShadow;
	box-shadow: 2px 2px 1px $defaultBoxShadow;
	-ms-border-radius: 3px;
	border-radius: 3px;
}

div#StatusDetailsDiv,
div#ImportSummaryDiv
{
	@extend .cardTable;
}



.cardRelativityControl{
	margin-left:20px;
}

div#ImportStatusSummary{
	@extend .cardRelativityControl;
}

//This is the container for the console
.right-nav-container,
#_licenseInfoContainer .rightNavActionBar{
	@extend .cardTable;
	border: thin solid $cardSectionBorder !important;
	>table{
		border: none !important;
		box-shadow: none !important;
		>tbody>tr>td{
			border: none !important;
			box-shadow: none !important;
		}
	}
}

#_license_nameCell{
	width:100px !important;
}


.rightNavActionBar .consoleControlTitle{
	border: none !important;
	box-shadow: none !important;
}

.cardContainerTemplate {
	background: $defaultWhite;
	padding: 10px;
	text-align: left;
	}

.cardContainerNoUpperPadding {
	@extend .cardContainerTemplate;
	padding-top: 0 !important;
}

/* This makes dtsearch view page look better but sadly removes tha margins in other pages (like ocr profiles)
#_dynamicTemplate__kCuraScrollingDiv .cardContainer{
	padding:0 !important;
}
*/
.formdiv{
	@extend .cardContainerTemplate;
}

.childViewSectionTitleText{
	@extend .cardSectionHeader;
}

div#StatusDetailsDiv .associativeListControl .itemListAssociativeTitleCell{
	padding-left:0;
	.itemListActionCell{
		margin:0 !important;
		padding-left:0 !important;
	}

}

	.fieldblock{
	@extend .cardTable;
	.sectionHeader{
		@extend .cardSectionHeader;
		background:transparent;
		border:none;
	}
	.fieldheader{
		@extend .cardLabel;
		background:$cardBGColor;
		border:none;
	}
	.fieldcontrol{
		@extend .cardValue;
		label{
			color:$defaultFont;
		}
	}
	}
	.fieldblocktable{
	background:transparent;
	}

.custom-filter-table{
	@extend .cardTable;
	width: 97% !important;
	margin-left:auto;
	margin-right:auto;
	margin-top:10px;
	input[type="text"], 
	input[type="submit"]{
		height:24px !important;
	}
	td.custom-filter-hr{
		display:none;
	}
	.custom-filter-hr-table > tbody > tr >td:first-child {
		@extend .cardLabel;
		text-transform:capitalize;
	}
	> tbody > tr > td {
		@extend .cardSectionHeader;
	}

}


