/* Sampling start */

.pivotmessagebar[id$="_samplingmessagebar"],
.pivotmessagebar .message {
	background-color:$samplingMessageBarBGColor;
	color:$samplingMessageBarColor !important;
}

.samplingContainer span[id$="samplingTypeLabel"] {
	color:$samplingFontColor;
	font-weight:600;
	padding-right:8px;
	margin-left:10px;
	font-size:13px;
	height:28px;			//the following fixes span moving around
	line-height:28px;
}

.samplingContainer {		// prevent sampling bar from expanding when these choices are selected
	select[id$="_confidenceLevelDropDown"],
	select[id$="_marginOfErrorDropDown"] {
		margin-top:-3px;
		height:26px !important;
	}
}

.samplingContainer span.fixedSizeSpan,
.samplingContainer span.percentSpan,
.samplingContainer span.confidenceSpan {
	margin-left:0 !important;
	&:before {
		content:"";
		border-left:1px solid $samplingDividerColor;
		height:22px;
		margin-left:15px;
		margin-right:18px;
	}
}

.samplingContainer {
	background:$samplingBGColor;
	border-top:0px solid $samplingBorderColor;
	border-bottom: 0px solid $samplingBorderColor;
	> table {
		border-top-color:$defaultBorderColor !important;
	}
}

.samplingContainer label {
	color:$samplingFontColor;
	font-weight:400;
}

.samplingContainer td {
	padding-top: 2px;
}

.samplingButtonCell {
	margin-top: 4px;
	margin-right: 20px;
}


/* Sampling end */